.middle-Desktop {
  padding: 0.5% 1%;
}
.footer {
  margin-top: 10%;
}
.quick {
  padding: 0% auto;
  padding-top: 2%;
  margin: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.quick p {
  font-size: 20px;
  font-family: 'Roboto Slab', sans-serif;
  color: #e1dfdd;
}

.Hidden-Footer {
  display: none;
}
.logo1-Top-Footer {
  height: 100%;
  padding: 3%;

  background-color: #00bcf2;
}

.logo1-Footer {
  height: 100%;
  padding: 0%;

  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #00bcf2;
}

.techHub-Footer {
  font-family: 'Roboto Slab', sans-serif;
  margin: 0.6rem auto;
  color: #e1dfdd;
}

.ideaSquare-Footer {
  margin: 0.6rem auto;
  margin-right: 5%;
  color: #e1dfdd;
  font-family: 'Roboto Slab', sans-serif;
}

.logo2-Footer {
  display: flex;

  align-items: center;
  background-color: #b4a0ff;
  justify-content: center;
  padding: 0%;
}
.logo3-Footer {
  display: flex;

  justify-content: center;
  padding: 0%;
  align-items: center;
  background-color: #00b294;
}

.home p {
  margin: 8px auto;
  height: 2rem;
  width: 100%;
}

.home p:hover {
  color: white;

  transition: 0.3s, ease;
  cursor: pointer;
}

