.icons-Footer {
  font-size: 30px;
  display: flex;
  align-items: center;
}
.home {
  font-family: 'Roboto Slab', sans-serif;
  padding: 1px;
  text-align: center;
  font-size: 0.9rem;
  align-items: center;
  text-align: center;
  color: #a19f9d;
}

.links-Mobile {
  font-family: 'Roboto Slab', sans-serif;
  padding: 0px;
  text-align: center;
  font-size: 11px;
  align-items: center;
  text-align: center;
  color: #a19f9d;
}

@media (max-width: 1040px) {
  .home {
    font-size: 0.8rem;
  }
  .icons-Footer {
    font-size: 28px;
  }
}

@media (max-width: 940px) {
  .home {
    font-size: 0.7rem;
  }
  .icons-Footer {
    font-size: 24px;
  }
}

@media (max-width: 850px) {
  .icons-Footer {
    font-size: 22px;
  }
}

@media (max-width: 730px) {
  .middle-Desktop {
    display: none;
  }
  .top-div-Footer {
    display: none;
  }
  .bottom-Footer-Desktop {
    display: none;
  }
  .ideaSquare-Footer {
    margin: 0.6rem auto;
    margin-right: 2%;
    font-size: 12px;
    color: #e1dfdd;
    font-family: 'Roboto Slab', sans-serif;
  }

  .Hidden-Footer {
    color: #a19f9d;
    display: grid;
    grid-template-columns: repeat(7, auto);
    font-size: 18px;
  }
  .Hidden-Footer p {
    margin: 3px;
  }
  .Hidden-Footer p:hover {
    color: white;

    transition: 0.3s, ease;
    cursor: pointer;
  }
}

@media (min-width: 730px) {
  .bottom-Footer-Mobile {
    display: none;
  }
}

@media (max-width: 630px) {
  .Hidden-Footer {
    font-size: 16px;
  }
}

@media (max-width: 530px) {
  .Hidden-Footer {
    font-size: 13px;
  }
}

@media (max-width: 450px) {
  .Hidden-Footer {
    font-size: 11px;
  }
}
@media (max-width: 390px) {
  .Hidden-Footer {
    font-size: 10px;
  }
}
@media (max-width: 360px) {
  .Hidden-Footer {
    font-size: 2.7vw;
  }
  .ideaSquare-Footer {
    font-size: 11px;
  }
}
